.toastBack {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;

    .toastCard {
        z-index: 101;
        min-width: 34rem;
        min-height: 10rem;
        background: #000000;
        border-radius: 0.9rem;
        background-color: rgba(0, 0, 0, 0.7);
        display: flex;
        justify-content: center;
        align-items: center;

        .toastText {
            font-size: 2.8rem;
            color: #ffffff;
        }
    }
}
