.page {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 100%;
    overflow-y: auto;

    .logo {
        align-self: flex-start;
        width: 11.4rem;
        height: 4.6rem;
        margin-top: 4rem;
        margin-left: 4rem;
    }

    .main {
        width: 69rem;
        margin-top: 3rem;

        // pc
        @media screen and (min-width: 769px) {
            width: 100rem;
        }
    }

    .from {
        display: flex;
        flex-direction: column;
        padding: 0 1rem;

        .formTitle {
            align-self: center;
            font-size: 3.6rem;
            font-weight: 600;
            color: #ffeed9;
        }

        .list {
            margin-top: 4rem;
            display: flex;
            flex-direction: column;

            .fromItemBar {
                @media screen and (min-width: 769px) {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: space-between;
                }
            }

            .item {
                margin-top: 4rem;
                display: flex;
                flex-direction: row;
                align-items: center;

                .itemTitle {
                    font-size: 2.8rem;
                    color: #a19890;
                    width: 20rem;
                }

                .itemValue {
                    font-size: 4.2rem;
                    color: #ebba81;
                }

                .itemValueId {
                    font-size: 2.8rem;
                    color: #ffeed9;
                }

                .copy {
                    cursor: pointer;
                    margin-left: 1.6rem;
                    width: 3rem;
                }

                .itemAmount {
                    margin-left: 1.6rem;
                    font-size: 2.8rem;
                    color: #a19890;
                }
            }
        }
    }

    .qrCardView {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 5rem;
        background: #3b3936;
        box-shadow: 0 0.4rem 2.2rem 0 rgba(27, 27, 27, 0.36);
        border-radius: 1.6rem;
        padding: 3rem;

        @media screen and (min-width: 769px) {
            align-items: flex-start;
        }

        .buttons {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: 6rem;

            @media screen and (min-width: 769px) {
                margin-bottom: 4rem;
            }
        }

        .button {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 30rem;
            height: 5.4rem;
            margin-left: 3rem;

            &:first-child {
                margin-left: 0rem;
            }

            background: rgba(36, 168, 232, 0);
            border: 0.2rem solid rgba(255, 217, 173, 0.2);
            border-radius: 1rem;
            cursor: pointer;
            user-select: none;

            font-size: 2.8rem;
            font-weight: 500;
            color: #ffd9ad;

            &.isActive {
                background: #ebba81;
                color: #32302d;
            }
        }

        .qrCardInfo {
            display: flex;
            flex-direction: column;
            align-items: center;

            @media screen and (min-width: 769px) {
                flex-direction: row;
            }
        }

        .qrCodeTitle {
            margin-bottom: 2.7rem;
            font-size: 2.4rem;
            color: #a19890;

            @media screen and (min-width: 769px) {
                margin-bottom: 1.4rem;
            }
        }

        .qrTexts {
            margin-top: 5.2rem;
            font-size: 2.6rem;
            font-weight: 400;
            color: #ffeed9;
            line-height: 4.2rem;

            @media screen and (min-width: 769px) {
                font-size: 1.4rem;
                margin-top: 0;
                margin-left: 5rem;
            }

            .qrTextItem {
                display: flex;
                flex-direction: row;

                .qrTextNum {
                    min-width: 3rem;
                }
            }
        }
    }

    .useList {
        margin-top: 10rem;
        margin-bottom: 10rem;
        display: flex;
        flex-direction: column;

        .useListTitle {
            align-self: center;
            font-size: 4rem;
            font-weight: 600;
            color: #ffeed9;
        }

        .useListItem {
            margin-top: 6rem;

            .useItemTitleBar {
                .useItemTitle {
                    font-size: 3rem;
                    line-height: 4.2rem;
                    color: #ffeed9;
                }

                .scan {
                    vertical-align: middle;
                    margin-left: 1rem;
                    margin-right: 1rem;
                    width: 5rem;
                    height: 5rem;
                }
            }

            .useItemText {
                margin-left: 2.3rem;
                font-size: 3rem;
                line-height: 4.2rem;
                color: #a19890;
            }

            .useItemInfo {
                margin-top: 3.6rem;
                margin-left: 2.3rem;
                display: flex;
                flex-direction: row;
                align-items: center;

                .useItemInfoText {
                    font-size: 2.8rem;
                    line-height: 4.2rem;
                    color: #a19890;
                    margin: 0 4.8rem;
                }

                .appLogo {
                    cursor: pointer;
                    width: 16.2rem;
                }

                .infoImg {
                    width: 36rem;
                }
            }
        }
    }
}
