@function getWidth1920($width) {
    @return calc($width * 100vw / 1920);
}

@function getWidth750($width) {
    @return calc($width * 100vw / 750);
}

html,
body {
    font-size: getWidth750(10);
    // pc
    @media screen and (min-width: 769px) {
        font-size: getWidth1920(10);
    }
    // 最大畫面1200px
    @media screen and (min-width: 1200px) {
        font-size: 7.5px;
    }

    margin: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    font-family: 'PingFangSC-Regular';

    // input上下箭頭隱藏
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }
    input[type='number'] {
        -moz-appearance: textfield;
    }
}

#root {
    height: 100%;
    width: 100%;
}

.App {
    width: 100%;
    height: 100%;
    background-color: #32302d;
}
