.mobilePage {
    background-color: #000000;
    height: 100vh;
    max-height: 100%;
    overflow-y: auto;

    .mainBg {
        background-image: url('../../assets/bg.png');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: top center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .logo {
        margin-top: 7.9rem;
        width: 14.9rem;
        height: 6rem;
    }

    .bgLogo {
        margin-top: 9.6rem;
        width: 55rem;
        height: 55rem;
    }

    .logoText {
        margin-top: 4.9rem;
        font-size: 5.2rem;
        font-weight: 600;
        color: rgba(255, 217, 173, 0.9);
    }

    .button {
        margin-top: 12.6rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 55rem;
        min-height: 9rem;
        background: #ebba81;
        border-radius: 4.5rem;
        cursor: pointer;
        user-select: none;

        font-size: 3.2rem;
        font-weight: 500;
        color: #32302d;

        &:active {
            opacity: 0.8;
        }
    }

    .list {
        width: 55rem;
        margin-top: 12.6rem;

        .item {
            margin-bottom: 8.8rem;

            .itemTitle {
                font-size: 3rem;
                color: #f5f6f7;
            }

            .itemImg {
                margin-top: 3rem;
                width: 100%;
                height: auto;
            }
        }
    }

    .iframe {
        display: none;
        height: 0;
    }
}

.pcPage {
    background-color: #000000;
    height: 100vh;
    max-height: 100%;

    .pcBg {
        position: relative;
        width: 100%;
        height: auto;
    }

    .mainBg {
        top: 0;
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        max-height: 100vh;
        overflow-y: auto;

        .mainBgInfo {
            margin-top: 8rem;
            display: flex;
            flex-direction: row;

            .mainBgInfoLeft {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-right: 6.8rem;

                .logo {
                    width: 12.5rem;
                    height: 5rem;
                }

                .logoText {
                    margin-top: 5rem;
                    font-size: 5rem;
                    font-weight: 500;
                    color: rgba(255, 217, 173, 0.9);
                }

                .qrCode {
                    margin-top: 6.6rem;
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    .qrCodeText {
                        margin-top: 2rem;
                        font-size: 1.4rem;
                        color: #a19890;
                    }
                }
            }

            .mainBgInfoRight {
                width: 66.2rem;
                height: 50.4rem;
            }
        }
    }

    .list {
        width: 118rem;
        margin-top: 15rem;
        margin-bottom: 15rem;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 8.4rem 14.7rem;

        .item {
            width: 30rem;

            .itemTitle {
                font-size: 1.5rem;
                color: #f5f6f7;
            }

            .itemImg {
                margin-top: 1rem;
                width: 100%;
                height: auto;
            }
        }
    }
}
